.questionnaire {
  width: 100%;
  padding-top: 27px;
  padding-bottom: 32px;


  .questionnaire__title-wrapper {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      background-color: var(--grey5);
      top: -27px;
      left: 0;
    }
  }

  .questionnaire__title {
    margin: 0;
    width: 100%;
    margin-bottom: 38px;
    color: var(--default-black);
  }

  .questionnaire__text {
    margin: 0;
    display: block;
    font-size: 14px;
    line-height: 18px;
    text-transform: uppercase;
    color: var(--default-black);
    margin-bottom: 38px;
  }

  .questionnaire__form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  @include tablet-w () {
    padding-top: 65px;
    padding-bottom: 72px;

    .questionnaire__title-wrapper {
      padding-left: var(--tablet-form-l-padding);
      padding-right: var(--tablet-form-r-padding);

      &::before {
        content: none;
      }
    }

    .questionnaire__title {
      font-size: 60px;
      line-height: 60px;
      color: var(--default-black);
      margin-bottom: 54px;
    }

    .questionnaire__text {
      font-size: 20px;
      line-height: 30px;
      margin-bottom: 66px;
      width: 323px;
    }
  }

  @include desktop-w () {
    padding-top: 77px;
    padding-bottom: 69px;

    .questionnaire__title-wrapper {
      padding-left: var(--desktop-padding);
      padding-right: var(--desktop-padding);
    }

    .questionnaire__title {
      margin-bottom: 57px;
    }

    .questionnaire__text {
      margin-bottom: 66px;
      width: 323px;
    }

    .questionnaire__form {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      column-gap: 80px;
    }
  }
}
